body{
  padding-top: 50px;
}
.calculator {
  max-width: 200px;
  margin: 0 auto;
  background-color: black;
  box-shadow: 10px 10px 8px #888888;
  padding: 10px;
  border-radius: 5px;
}

.ekran {
  background-color: #ffffff;
  margin-bottom: 10px;
  text-align: right;
  padding: 5px;
  height: 20px;
}

.kinobka button {
  width: 40px;
  height: 40px;
  margin: 2px;
  color: #ffffff;
  border: none;
  cursor: pointer;
}
.highlight {
  background-color: #2196f3;
}

#clear {
  background-color: #ff9800;
}

#result {
  background-color: #ff5722;
}
